import React from 'react';
import {Button,TabBar} from 'antd-mobile'
import {withRouter} from 'react-router-dom'
import './taobao.css'
class Lasttab extends React.Component {
	constructor(props) {
	  super(props);
	  this.state = {
		selectedTab: 'blueTab',
	  };
	}
  
	render() {
		const {pathname} = this.props.location;
		console.log(pathname);
		return (
          <div className='tabs' style={{ position: 'fixed', width: '100%', bottom: 0 }}>	
			<div style={{width:'40%',float:'left'}}> 
                <TabBar                  
                    unselectedTintColor="#949494"
                    tintColor="grey"
                    barTintColor="white"
                >               
                    <TabBar.Item
                        title="店铺"
                        key="shop"
                        icon={
                            <i style={{fontSize:22}} className='iconfont icon-dianpu'></i> 
                        
                        }
                        selectedIcon={
                        <i style={{fontSize:22}} className='iconfont icon-dianpu-tap'></i> 
                        }
                        selected={pathname === '/details/shop'}
                        onPress={() => {
                            this.props.history.push('/details/shop');

                        }}
                        data-seed="logId"
                        >				
                    </TabBar.Item>
                    
                    <TabBar.Item				
                        icon={
                            <i style={{fontSize:22}} className='iconfont icon-kefu'></i> 
                        }
                        selectedIcon={
                            <i style={{fontSize:22}} className='iconfont icon-htmal5icon29'></i> 
                        }
                        title="客服"
                        key="Kefu"
                        selected={pathname === '/details/servicer'}
                        onPress={() => {
                            this.props.history.push('/details/servicer')
                        }}
                        data-seed="logId1"
                        >
                    </TabBar.Item>

                    <TabBar.Item
                        icon={
                            <i style={{fontSize:22}} className='iconfont icon-shoucang1'></i> 
                        }
                        title="收藏"
                        key="collect"
                        onPress={() => {
                            this.props.history.push('/details/colletion')
                        }}
                        > 
                    </TabBar.Item>
			    </TabBar>
            </div>
            <div className='tabbox' style={{float:'left',width:'58%'}}>
               <div 
                  className='left' 
                  onClick={() => {
					this.props.history.push('/cart')
				   }}
                >加入购物车           
                </div>
               <div 
                 className='right'
                 onClick={() => {
                    this.props.history.push('/details/buys')
                 }}
               >立即购买</div>
            </div>            
		</div>           
	    );
	}
  }
  

  export default withRouter(Lasttab)